@media only screen and (max-width: 767px) { .content figure + figure { margin-top: -34px; } }

.content a[href^="tel:"] { text-decoration: none; font-size: 1.25rem; font-family: 'proxima-nova-semibold', sans-serif; color: #30302F; border-bottom: 1px solid transparent; }

.content a[href^="tel:"]:hover, .content a[href^="tel:"]:focus { border-bottom-color: currentColor; }

@media only screen and (max-width: 767px) { .content a[href^="tel:"] { font-size: 1.0625rem; } }

.content .table { border-collapse: collapse; margin: 45px 0; }

@media only screen and (max-width: 767px) { .content .table { margin: 27px 0; font-size: 0.875rem; } }

.content .table--layout-fixed { table-layout: fixed; }

.content .table th { font-family: 'proxima-nova-semibold', sans-serif; background: #FDD80C; color: #283344; }

@media only screen and (max-width: 767px) { .content .table th { vertical-align: middle; font-size: 0.75rem; } }

.content .table th, .content .table td { padding: 13px 10px; border: 1px solid #283344; text-align: center; }

@media only screen and (max-width: 767px) { .content .table th, .content .table td { padding: 10px 5px; } }

.content .table th small { display: block; }

.content .table td[colspan] { padding: 5px; font-family: 'proxima-nova-semibold', sans-serif; background: #FDD80C; }

.content .table .cell--text-left { text-align: left; }

.content .table .cell--set-w-240 { width: 240px; }

@media only screen and (max-width: 1024px) { .content .table .cell--set-w-240 { width: auto; } }

.content .table .cell--set-w-120 { width: 120px; }

@media only screen and (max-width: 767px) { .content .table .cell--set-w-120 { width: auto; } }

.content .table .cell--with-icon { position: relative; }

.content .table .cell--with-icon div { position: absolute; top: 50%; right: 20%; color: #03B1FC; transform: translateY(-50%); }

@media only screen and (max-width: 1024px) { .content .table .cell--with-icon div { display: none; } }

.content .table .cell--with-icon svg { vertical-align: middle; font-size: 17px; fill: #03B1FC; }

.content .table .cell--with-icon span { vertical-align: middle; }

.content .table + .notes { margin: -25px 0 40px; }

@media only screen and (max-width: 767px) { .content .table + .notes { margin: -10px 0 27px; } }

.content .time { position: relative; margin-bottom: 25px; padding-left: 15px; font-family: 'proxima-nova-regular-italic', sans-serif; }

.content .time:before { content: ''; position: absolute; top: -8px; left: 0; width: 5px; height: 38px; background: #FDD80C; }

@media only screen and (max-width: 767px) { .content .time:before { top: -5px; height: 30px; } }

.content .time b { font-family: 'proxima-nova-semibold', sans-serif; color: #111110; }

.content .notes p { margin-bottom: 0; }

.content .notes b { font-family: 'proxima-nova-semibold', sans-serif; color: #FF001F; }

.content .list-group { display: flex; margin: 0 -10px; }

@media only screen and (max-width: 1024px) { .content .list-group { flex-wrap: wrap; } }

.content .list-group div { margin: 0 10px; padding: 25px 10px 25px 15px; border: 3px solid #FDD80C; }

.content .list-group div:first-child { flex-basis: 50%; }

.content .list-group div:first-child ul li:before { background-image: url(../icons/checkmark-blue.svg); }

.content .list-group div:last-child { flex-basis: 50%; margin-bottom: 0; }

.content .list-group div:only-child { flex-basis: 100%; }

@media only screen and (max-width: 1024px) { .content .list-group div { margin-bottom: 20px; }
  .content .list-group div:first-child, .content .list-group div:last-child { flex-basis: 100%; } }

@media only screen and (max-width: 767px) { .content .list-group div { padding: 20px 10px 20px 15px; } }

.content .list-group p { margin-bottom: 16px; }

.content .list-group b { font-family: 'proxima-nova-semibold', sans-serif; }

.content .list-group ul li { position: relative; margin-bottom: 10px; padding-left: 29px; }

.content .list-group ul li:before { content: ''; position: absolute; top: 7px; left: 0; width: 17px; height: 11px; background-image: url(../icons/checkmark-pink.svg); background-size: contain; background-repeat: no-repeat; }

.content .list-group ul li:last-child { margin-bottom: 0; }

.content .button { display: block; max-width: 209px; margin: 40px auto 0; padding: 13px; text-align: center; }

@media only screen and (max-width: 767px) { .content .button { max-width: 320px; } }

.content .button--mob { display: none; }

@media only screen and (max-width: 767px) { .content .button--mob { display: block; } }

@media only screen and (max-width: 767px) { .content .button + h3 { margin-top: 30px; } }
